// Importacion de elementos
@import url('../fonts/material-icons.css');

@import "mixins";
@import "variables";

// --------------------------------------------
:root {
    font-size: 13px;
}
// +++++++++++++++++++++++++++++++++++++++++++++

body {
    background: {
        color: #EFEFEF;
    }
    color: #444;
    font: {
        family: Arial, "Helvetica Neue", Helvetica, sans-serif;
        size: 1rem;
        weight: 500;
    }
    line-height: 1.5;
}


.text-color1 {
    color: $color1;
}

.text-color2 {
    color: $color2;
}

.bg-color1 {
    background-color: $color1;

    .card-header{
        background-color: $color1;
    }
}

.bg-color2 {
    background-color: $color2;
}

.border-color1 {
    border-color: $color1 !important;
}

.border-right{
    border-right: 1px solid #DDD;
}

.alert-color1 {
    color: #444;
    background-color: #FFF;
    border-color: $color1;

    a{
        
        text-decoration: underline;
        font-weight: 700;

        &:link, &:visited{
            color: $color1;
        }

        &:hover, &:active{
            color: lighten($color1, 5%);
        }
    }
}

a {
    &:link,
    &:visited {
        color: #444;
    }
    &:hover,
    &:active {
        color: #000;
    }
}

strong,
b,
.form-group label {
    font-weight: 700;
}

.relative {
    position: relative;
}

.col-form-label {
    font-weight: 700;
}

.btn-color1 {
    background: $color1;
    color: #ffffff !important;
    border: 1px solid $color1;

    &:hover,
    &:focus,
    &:active,
    &.active {
        background: lighten($color1, 5%);
        border: 1px solid lighten($color1, 3%);
    }
}

.btn-color2 {
    background: $color2;
    color: #ffffff !important;
    border: 1px solid $color2;

    &:hover,
    &:focus,
    &:active,
    &.active {
        background: lighten($color2, 5%);
        border: 1px solid lighten($color2, 3%);
    }
}

$i: 2;
@while $i < 100 {
    .material-icons.md-#{$i} {
        font-size: #{$i}px;
    }
    $i: $i+2;
}

@for $i from 1 through 6 {

    h#{$i} {

        font-weight: 700;
        color: #444;

        @if $i==1 {

            font-size: 3.5rem;
        }
        @else if $i==2 {

            font-size: 3rem;
        }
        @else if $i==3 {

            font-size: 2.5rem;
        }
        @else if $i==4 {

            font-size: 2rem;
        }
        @else if $i==5 {

            font-size: 1.5rem;
        }
        @else {

            font-size: 1rem;
        }
    }
}

// //////////////////////////////////////////////////
// // ///////////////////////////////////////////////
// // ///////////////////////////////////////////////

header{
    .navbar-brand{
        font-size: 18px;
    }
    .btn-light{
        background-color: #F2F2F2;
        border: 1px solid #DDD;
        -webkit-box-shadow: 0 1px 1px 1px rgba(0,0,0,0.1);
                box-shadow: 0 1px 1px 1px rgba(0,0,0,0.1);
    }
    .navbar{
        -webkit-box-shadow:inset 0 2px 2px 2px rgba(0,0,0,0.1);
                box-shadow:inset 0 2px 2px 2px rgba(0,0,0,0.1);

        .navbar-nav{
            .nav-item{
                border-left: 1px solid #FFF;
                border-right: 1px solid #FFF;
                font-size: 15px;

                .nav-link{
                    &:link, &:visited{
                        color: #FFF;
                    }
                    &:hover, &:active{
                        color: #FFF;
                        background-color: darken($color1, 5%);
                    }
                }
            }
        }

    }
    
}

.card-header{
    font-size: 1.2rem;
    font-weight: 700;
    position: relative;
    background-color: #FFF;

    .material-icons{
        position: absolute;
        right: 10px;
        top: 10px;
    }
}

[data-toggle="collapse"] .material-icons:before {  
  content: "\E316";
}

[data-toggle="collapse"].collapsed .material-icons:before {
  content: "\E313";
}

.nav-fill{
    .nav-item{
        text-align: left;
        font-size: 1.2rem;
        line-height: 120%;


        .nav-link{
            background-color: #FFF;
            border: 1px solid #C8C8C8;
            height: 70px;
            position: relative;
            
            &.new{
                border: 1px solid $color1;
            }

            &.active{
                border: 1px solid $color1;
                border-bottom: 1px solid #FFF;
                color: #444;
                position: relative;
                z-index: 10;
            }

            vertical-align: middle;
        }
    }
}
.tab-content{
    .tab-pane{
        border: 1px solid $color1;
        background-color: #FFF;
        position: relative;
        top: -1.7rem;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 9;
    }
}


.indicadores{
    position: absolute;
    top: 5px;
    left: 33px;
    font-size: 1.1rem;
    span:first-child{
        background-color: $color1;
        border-radius: .6rem 0 0 .6rem;
    }
    span:last-child{
        background-color: #999;
        border-radius: 0 .6rem .6rem 0;
    }
}

.timer{
    font-size: 30px !important;
    fill: #444 !important;
    margin-top: -5px;
}

footer{
    border-top: 1px solid #CCC;
}
